.agree-section {
  /* margin: 1rem 0; */
  /*아래 확인후 지우기*/
  /* margin: 1rem 0 0 0; */
  /* border: 1px solid red; */

  justify-content: center;
  align-items: center;
  background-color: #f1f3f5;
}

.agree-container {
  width: 100%;
  margin: 1rem 0;
  border: 1px solid rgba(46, 43, 43, 0.5);
  border-radius: 0.25rem;
  background-color: #fff;
  /* border: 1px solid blue; */
}

.agree-row-wrapper {
  align-items: stretch;
}
.agree-row-wrapper h4 {
  margin: 0.5rem 0;
  padding: 0.5rem 0.75rem;
}
.agree-center {
  /* width: 100%; */
  /* border: 2px solid purple; */
  /* border: 1px solid red; */
}
.agree-label {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  margin: 0.25rem 0.5rem;
  text-align: center;
  /* border: 1px solid blue; */
  /**/
  /* margin: 0.25rem; */
}
.agree-center,
.required-label:nth-child(n),
.optional-label:nth-child(n) {
  /* border: 1px solid red; */
  margin-bottom: 0.5rem;
  /* margin: 0.5rem 0; */
}
.required-label:last-child,
.optional-label:last-child {
  /* margin-bottom: 0.75rem; */
}
.required-label:first-child,
.optional-label:first-child {
  /* margin-top: 0.5rem; */
}
.agree-input {
  width: auto;
  margin: 0 0.5rem;
}

.optional-center,
.required-center {
  display: flex;
  flex-direction: column;
  /* border: 2px solid yellow; */
}

.form-btn {
  display: block;
  width: 100%;
  font-size: 1rem;
  color: #000;
  cursor: pointer;
}
@media screen and (max-width: 479px) {
  /*agree-section 너비 확인후  지우기*/
  .agree-section {
    max-width: 350px;
  }
  .agree-title {
    padding: 1rem 0.25rem;

    /* border: 1px solid red; */
  }
  .agree-row-wrapper {
    padding: 0.25rem 0;
  }
  .agree-row-wrapper h4 {
    /* padding: 0.5rem 0.5rem; */
    padding: 0.5rem 0.25rem;
    margin: 0.25rem 0;
    margin-bottom: 0;
  }
  .agree-label {
    padding: 0.5rem 0.5rem;
    margin: 0;
    /**/
    /* margin: 0.25rem 0; */
    padding: 0.5rem 0.25rem;
    /* padding: 0.5rem 0; */
    /* border: 1px solid red; */
  }
}
@media screen and (max-width: 767px) {
  /* .agree-section {
    max-width: 500px;
  } */
  .agree-container {
    max-width: 500px;
  }
  .agree-row-wrapper h4,
  .agree-label {
    font-size: 1rem;
  }
  /*확인하고 삭제 할지 말지  다시 수정해야함*/
  .agree-label {
    padding: 0.5rem 0;
  }
  .form-btn {
    font-size: 1rem;
  }
}
@media screen and (min-width: 768px) {
  /* .agree-section {
    max-width: 600px;
  } */
  .agree-container {
    max-width: 600px;
  }
  .agree-row-wrapper h4,
  .agree-label {
    font-size: 1.1rem;
  }

  .form-btn {
    font-size: 1.1rem;
  }
}
